import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import CardGrid from "@vkontakte/vkui/dist/es6/components/CardGrid/CardGrid";
import Card from "@vkontakte/vkui/dist/es6/components/Card/Card";

import './home.css';

const Home = ({ id, go, days, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader >Мой дневник</PanelHeader>
		{fetchedUser &&
		<Group title="Это вы :)">
			<Cell
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</Cell>
		</Group>}

		<Group>
			<Div>
				<Group>
					<CardGrid>

						{days.map((item, key) =>
							<Card size="l" mode="shadow">
								<div class="table">
									<div className="header">
										{item.dayTitle}
									</div>
									<div className="content">
										<div className="table">
											{item.lessons.map((item, key) => (
												<div className="row">
													<div className="cell index">
														<div>{key + 1}</div>
													</div>
													<div className="cell hw">
														<div className="subject">{item.subject}</div>
														<div className="small-hw">Добавить ДЗ</div>
													</div>
													<div className="cell grade" class={
														item.grade == 4 || item.grade == 5 ?
															'cell grade good'
															: 'cell grade bad'}>
														<div>{ item.grade }</div>
													</div>
												</div>
												)
											)}
										</div>
									</div>
								</div>
							</Card>
						)}

					</CardGrid>
				</Group>
			</Div>
			<Div>

				<Button size="xl" level="2" onClick={go} data-to="persik">
					Персика, пожалуйста
				</Button>

				<Button size="xl" level="2" onClick={go} data-to="add" style={{marginTop:10}}>
					Добавить ДЗ
				</Button>

			</Div>
		</Group>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
